import { createRouter, createWebHashHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
    meta: { title: "合同会社エクスシップス" },
  },
  // {
  //   path: "/about",
  //   name: "about",
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () =>
  //     import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
  // },
  {
    path: "/company",
    name: "company",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "company" */ "../views/CompanyView.vue"),
    meta: { title: "会社概要" },
  },
  {
    path: "/projects",
    name: "projects",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "company" */ "../views/ProjectsView.vue"),
    meta: { title: "会社事業" },
  },
  {
    path: "/contact",
    name: "contact",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "contact" */ "../views/ContactView.vue"),
    meta: { title: "お問い合わせ" },
  },
  // {
  //   path: "/thank",
  //   name: "thank",
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () =>
  //     import(/* webpackChunkName: "thank" */ "../views/ThankView.vue"),
  // },
  {
    path: "/privacy_policy",
    name: "privacy_policy",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "thank" */ "../views/PrivacyPolicyView.vue"),
    meta: { title: "プライバシーポリシー" },
  },
  {
    path: "/terms_of_use",
    name: "terms_of_use",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "thank" */ "../views/TermsOfUseView.vue"),
    meta: { title: "利用規約" },
  },
  // {
  //   path: "/debug",
  //   name: "debug",
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () =>
  //     import(/* webpackChunkName: "thank" */ "../views/DebugView.vue"),
  //   meta: { title: "デバッグ" },
  // },
  {
    path: "/message",
    name: "message",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "thank" */ "../views/MessageView.vue"),
    meta: { title: "会社メッセージ" },
  },
];

const router = createRouter({
  //history: createWebHistory(process.env.BASE_URL),
  history: createWebHashHistory(),
  routes,
  scrollBehavior(to, from, savedPosition) {
    // savedPosition がある場合（ブラウザ戻るボタンなど）
    if (savedPosition) {
      return savedPosition;
    }
    // ページ遷移したら常にトップにスクロール
    return { top: 0 };
  },
});

// ページ遷移時にタイトルを動的変更
router.afterEach((to) => {
  const baseTitle = "合同会社エクスシップス";
  if (to.meta.title) {
    document.title = `${to.meta.title} | ${baseTitle}`;
  } else {
    document.title = baseTitle;
  }
});

export default router;
