<template>
  <!-- 🌌 Animated Hero Section -->
  <section class="hero-section" ref="hero">
    <div class="stars"></div>
    <div class="twinkling"></div>
    <div class="ocean"></div>

    <div class="hero-content" :class="{ visible: heroVisible }">
      <transition name="fade-slide" appear>
        <div v-if="heroVisible">
          <h1>ITで「真の価値」を届ける</h1>
          <p>合同会社エクスシップス — exships LLC.</p>
          <v-btn
            color="indigo"
            variant="flat"
            size="large"
            class="mt-6"
            @click="scrollToFeatures"
          >
            強みを見る
          </v-btn>
        </div>
      </transition>
    </div>
  </section>

  <!-- 💼 Features Section -->
  <v-container class="mt-16 mb-16" ref="features">
    <v-row>
      <v-col
        v-for="(feature, i) in features"
        :key="i"
        cols="12"
        md="4"
        class="text-center"
      >
        <transition name="fade-up" appear>
          <v-card elevation="6" class="pa-6 hover-card">
            <v-icon size="48" color="primary" class="mb-4">
              {{ feature.icon }}
            </v-icon>
            <h3 class="font-weight-bold mb-2">{{ feature.title }}</h3>
            <p class="text-medium-emphasis">{{ feature.description }}</p>
          </v-card>
        </transition>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    heroVisible: false,
    features: [
      {
        icon: "mdi-cellphone-link",
        title: "クロスプラットフォーム開発",
        description:
          "iOS / Android / Web を横断し、高品質なアプリケーションを効率的に開発します。",
      },
      {
        icon: "mdi-brain",
        title: "AI・機械学習",
        description:
          "AI・ML技術を活用し、よりスマートで自律的なサービスを実現します。",
      },
      {
        icon: "mdi-chart-bubble",
        title: "チーム開発支援",
        description:
          "アジャイルとスクラムで、チームの力を最大化する仕組みを構築します。",
      },
    ],
  }),
  mounted() {
    setTimeout(() => (this.heroVisible = true), 600);
  },
  methods: {
    scrollToFeatures() {
      this.$nextTick(() => {
        const el = this.$refs.features?.$el; // Vuetify コンポーネントの DOM
        if (el && el.scrollIntoView) {
          el.scrollIntoView({ behavior: "smooth" });
        } else {
          console.warn(
            "Features element not found or scrollIntoView unavailable"
          );
        }
      });
    },
  },
};
</script>

